export default {
  install (Vue, options = {}) {
    // 自定义按钮指令
    Vue.directive('btnAuth', {
      inserted (el, binding) {
        if (options.store.state.btnPermission.includes(binding.value)) {
          el.style.display = 'block'
        } else {
          el.style.display = 'none'
        }
      }
    })
  }
}